import React from "react";
import Async from "react-code-splitting";

// core components
import DemoNavbar from "../../components/Navbars/DemoNavbar.jsx";
import CardsFooter from "../../components/Footers/CardsFooter.jsx";

// index page sections
const Hero = () => <Async load={import("../IndexSections/Hero.jsx")} />;
const BoxContent = () => (
  <Async load={import("../../components/Content/BoxContent.jsx")} />
);
const AboutMe = () => (
  <Async load={import("../../components/Content/AboutMe.jsx")} />
);
const Quote = () => (
  <Async load={import("../../components/Content/Quote.jsx")} />
);
const Web = () => <Async load={import("../../components/Content/Web.jsx")} />;
const Design = () => (
  <Async load={import("../../components/Content/Design.jsx")} />
);

class LandingPage extends React.Component {
  componentDidMount() {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    this.refs.main.scrollTop = 0;
  }
  render() {
    return (
      <>
        <DemoNavbar />
        <main ref="main" style={{ marginBottom: "100px" }}>
          <Hero />
          <Quote />
          <AboutMe />
          <BoxContent />
          <Web
            header="Web Development"
            desc="More than 30 Web Site (WordPress, OpenCart, CMS, HTML)"
          />
          <Design
            header="Design"
            desc="More than 50 Customer (Flyer, Brochure, Business Card, Mailing, Social Media Contents)"
          />
        </main>
        <CardsFooter />
      </>
    );
  }
}

export default LandingPage;
